<template>
  <div id="settings-advanced">
    <v-list two-line subheader>
      <v-subheader>发音</v-subheader>
      <v-list-item>
        <v-list-tile avatar>
          <v-list-tile-action>
            <v-checkbox v-model="clicksound"></v-checkbox>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>点击发音</v-list-tile-title>
            <v-list-tile-sub-title>允许点击单词发音</v-list-tile-sub-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list-item>
      <v-list-item>
        <v-list-tile avatar>
          <v-list-tile-action>
            <v-checkbox v-model="autoread"></v-checkbox>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>自动发音</v-list-tile-title>
            <v-list-tile-sub-title>允许单词切换后自动发音</v-list-tile-sub-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list-item>
      <v-list-item>
        <v-list-tile avatar>
          <v-list-tile-action>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              发音速度
              <v-select
                v-model="pronounceSpeed"
              required
              :items="[1,2,3,4,5,6,7]"
            ></v-select></v-list-tile-title>
            <v-list-tile-sub-title>数值越大发音速度越快</v-list-tile-sub-title>

          </v-list-tile-content>
        </v-list-tile>
      </v-list-item>

    </v-list>
  </div>
</template>

<script>
  import settings from '@/utils/settings'

  export default {
    data() {
      return {
        clicksound: false,
        autoread: false,
        pronounceSpeed: 3
      }
    },
    watch: {
      clicksound: function () {
        settings.saveSettingitem('clicksound', this.clicksound)
      },
      autoread: function () {
        settings.saveSettingitem('autoread', this.autoread)
      },
      pronounceSpeed: function () {
        settings.saveSettingitem("pronounceSpeed", this.pronounceSpeed)
      }
    },
    mounted () {
      if (settings.getSettingitem('clicksound') === null) {
        settings.saveSettingitem('clicksound', false);
      }
      if (settings.getSettingitem('autoread') === null) {
        settings.saveSettingitem('autoread', false);
      }
      if (settings.getSettingitem('pronounceSpeed') === null) {
        settings.saveSettingitem('pronounceSpeed', 3);
      }
      this.clicksound = settings.getSettingitem('clicksound');
      this.autoread = settings.getSettingitem('autoread');
      this.pronounceSpeed = settings.getSettingitem('pronounceSpeed');
    }
  }
</script>



